import React from 'react';
import { HashLink } from 'react-router-hash-link';
import './css/FooterNav.css';

export default function FooterNav(props) {
  const footerNavItems = props.links.map((link, index) => 
    <div className="footer-nav-item">
      <img src={`${process.env.PUBLIC_URL}/img/${props.linkImageUrl[index]}`} />
      <p style={{color:"orange"}}>{props.navHeading[index]}</p>
      <ul>
        {props.navContents[index].map((content, number) => 
          <li><HashLink smooth to={`${props.links[index]}#${props.contentsId[index][number]}`} onClick={props.currentPage !== index ? () => props.setCurrentPage(index): ""}>{content}</HashLink></li>
        )}
      </ul>
    </div>
  )

  return(
    <>
      <div className="footer-nav">
        {footerNavItems}
      </div>
    </>
  )
}