import React, { useState, useEffect } from 'react';
import './App.css';
import './Reset.css';
import Header from './components/Header';
import MainLeft from './components/MainLeft';
import MainRight from './components/MainRight';
import FooterNav from './components/FooterNav';
import FooterImage from './components/FooterImage';

function App() {
  const [currentPage, setCurrentPage] = useState(0);
  const [scrolled, setScrolled] = useState(0);

  const scrollComfirmation = () => {
    if(window.location.hash !== '#/contact/thanks'){
      for(let i = 0; i < contentsId[currentPage].length; i++){
        if(i !== contentsId[currentPage].length - 1){
          if(window.pageYOffset + 5 >= window.pageYOffset + document.getElementById(contentsId[currentPage][i]).getBoundingClientRect().top && window.pageYOffset < window.pageYOffset + document.getElementById(contentsId[currentPage][i + 1]).getBoundingClientRect().top){
            setScrolled(i);
          }
        } else {
          if(window.pageYOffset + 5 >= window.pageYOffset + document.getElementById(contentsId[currentPage][i]).getBoundingClientRect().top){
            setScrolled(i);
          }
        }
      }
    }
  }

  useEffect(() => {
    setScrolled(0);
    window.addEventListener('scroll',scrollComfirmation);
    return(() => {
      window.removeEventListener('scroll',scrollComfirmation);
    })
  },[currentPage])

  const links = ["/", "/aboutme", "/school", "/charge", "/chirashi", "/question", "/contact"];
  const linkImageUrl = ["icon-home.png","icon-aboutme.png","icon-school.png","icon-charge.png","icon-chirashi.png","icon-question.png","icon-contact.png"];
  const contentsId = [
    ["about","chirashi","lendingPC","corona"],
    ["introduction","character","instructor"],
    ["qualification","programming"],
    ["lesson","course","timetable"],
    ["createChirashi","createVideo"],
    ["questions"],
    ["contact"]
  ]
  const navHeading= ["ホーム","どんな教室？","学べること","具体的に","作成します","Q & A","お問い合わせ"];
  const navContents = [ 
    ["教室について","すずらんキャンペーンちらし","パソコンの貸し出しについて","新型コロナウイルス対策について"],
    ["教室紹介","教室の特徴","インストラクター紹介"],
    ["資格について","プログラミング講座"],
    ["料金体系","コース紹介","時間割"],
    ["ちらし作成します！","PR映像作成します！"],
    ["Q & A"],
    ["お問い合わせ"]
   ];

  return(
    <div className="App">
      <Header
        currentPage = {currentPage}
        setCurrentPage = {setCurrentPage}
        scrolled = {scrolled}
        setScrolled = {setScrolled}
        links = {links}
        navHeading = {navHeading}
        navContents = {navContents}
        contentsId = {contentsId}
      />
      <div className="main">
        <MainLeft
          currentPage = {currentPage}
          setCurrentPage = {setCurrentPage}
        />
        <MainRight />
      </div>
      <div className="footer">
        <FooterNav
          links = {links}
          linkImageUrl = {linkImageUrl}
          navHeading = {navHeading}
          navContents = {navContents}
          contentsId = {contentsId}
          currentPage = {currentPage}
          setCurrentPage = {setCurrentPage}
        />
        <FooterImage />
      </div>
    </div>
  )
}

export default App;