import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import './css/HeaderNav.css';

export default function HeaderNav(props) {
  const images = [
    "icon-home.png",
    "icon-aboutme.png",
    "icon-school.png",
    "icon-charge.png",
    "icon-chirashi.png",
    "icon-question.png",
    "icon-contact.png"
  ];

  const scrollEvent = () => {
    if(window.scrollY > 0){
      window.scrollTo(0, 0)
    }
  }

  const closeMenuEvent = () => {
    props.setClick(false);
  }

  const menuEvents = () => {
    scrollEvent();
    closeMenuEvent();
  }

  const reloadEvents = () => {
    for(let i = 0; i < props.links.length; i++){
      if(i === 0 && window.location.hash === `#${props.links[i]}`){
        props.setCurrentPage(i);
      } else if (i !== 0 && window.location.hash.match(props.links[i])) {
        props.setCurrentPage(i);
      }
    }
  }

  const headerTopEvents = () => {
    menuEvents();
    props.setCurrentPage(0);
  }

  useEffect(() => {
    menuEvents();
    window.addEventListener('popstate',reloadEvents);
    window.addEventListener('load',reloadEvents);
    document.getElementById('current').addEventListener('click',menuEvents);
    document.getElementById('headerLink').addEventListener('click',headerTopEvents);
    return(() => {
      window.removeEventListener('popstate',reloadEvents);
      window.removeEventListener('load',reloadEvents);
      document.getElementById('current').removeEventListener('click',menuEvents);
      document.getElementById('headerLink').removeEventListener('click',headerTopEvents);
    })
  },[props.currentPage])

  const linkItems = props.links.map((item,index) => 
    <>
      <li key={item.toString()} className={props.currentPage === index ? "nav-item current" : "nav-item"}>
        <Link to={item} onClick={props.currentPage !== index ? () => props.setCurrentPage(index) : ""} id={props.currentPage === index ? "current" : ""}>
          <div>
            <img src={`${process.env.PUBLIC_URL}/img/${images[index]}`} />
            <h1>{props.navHeading[index]}</h1>
          </div>
        </Link>
      </li>
    </>
  )

  return(
    <>
      <ul className={props.click ? 'header-nav active' : 'header-nav'}>
        {linkItems}
      </ul>
      <div className={props.click ? 'mask active' : 'mask'}></div>
    </>
  )
}