import React from 'react';
import { HashLink } from 'react-router-hash-link';
import './css/HeaderUnderNav.css';

export default function HeaderUnderNav(props) {
  const navItems = props.navContents[props.currentPage].map((item,index) => 
    <li className={props.scrolled === index ? "header-under-nav-item active" : "header-under-nav-item"}>
      <HashLink smooth to={`${props.links[props.currentPage]}#${props.contentsId[props.currentPage][index]}`} 
      >{item}</HashLink>
    </li>
  )

  return(
    <div className="header-under-nav">
      <p>{`${props.navHeading[props.currentPage]}　▶️`}</p>
      <ul>
        {navItems}
      </ul>
    </div>
  )
}