import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import HeaderNav from './HeaderNav';
import HeaderUnderNav from './HeaderUnderNav';
import './css/Header.css';

export default function Header(props){
  const [click, setClick] = useState(false);

  return(
    <>
      <div className="header" style={{backgroundImage: `url(${process.env.PUBLIC_URL}/img/header-sky2.png)`}}>
        <div className="header-top">
          <Link to="/" id="headerLink">
            <img src={`${process.env.PUBLIC_URL}/img/title-logo.png`} />
          </Link>
          <i className={click ? 'fas fa-times' : 'fas fa-bars'} onClick={() => setClick(!click)} />
        </div>
        <HeaderNav
          click = {click}
          setClick = {setClick}
          currentPage = {props.currentPage}
          setCurrentPage = {props.setCurrentPage}
          links = {props.links}
          contentsId = {props.contentsId}
          navHeading = {props.navHeading}
        />
        <HeaderUnderNav
          currentPage = {props.currentPage}
          setCurrentPage = {props.setCurrentPage}
          scrolled = {props.scrolled}
          setScrolled = {props.setScrolled}
          links = {props.links}
          contentsId = {props.contentsId}
          navHeading = {props.navHeading}
          navContents = {props.navContents}
        />
      </div>
    </>
  )
}