import React from "react";
import { Route, Switch } from 'react-router-dom';
import './css/MainLeft.css';
import Home from './Home/Home';
import Aboutme from './Aboutme/Aboutme';
import School from './School/School';
import Charge from './Charge/Charge';
import Create from './Create/Create';
import Question from './Question/Question';
import Contact from './Contact/Contact';
import Thanks from './Thanks/Thanks';

export default function MainLeft(props) {
  return(
    <>
      <div className="main-left">
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/aboutme" component={Aboutme} />
          <Route path="/school" component={School} />
          <Route path="/charge" component={Charge} />
          <Route path="/chirashi" component={Create} />
          <Route path="/question" render={() => <Question currentPage = {props.currentPage} setCurrentPage = {props.setCurrentPage} />} />
          <Route exact path="/contact" component={Contact} />
          <Route path="/contact/thanks" component={Thanks} />
        </Switch>
      </div>
    </>
  )
}